<template>
  <div style="height:100%;background:#F9F9F9;display:flex;flex-direction:column;">
    <navigation-bar  @onLeftClick="onClickLeft" pageName="我的房产">
    </navigation-bar>
    <div class="no-networking" v-show="networkError">
      <img :src="noNetworkImg">
      <h3>网络请求失败</h3>
      <p>请检查您的网络</p>
      <div @click="initMyRooms">重新加载</div>
    </div>
    <!--<div class="emptyView" v-if="list.length == 0 && showEmpty">
      <span style="color:#FB5768;font-size:14px;line-height:120px;">您当前还未绑定房源！</span>
    </div>-->
    <div class="noList" v-if="list.length == 0 && showEmpty" >
      <img src="../../assets/images/default/no_home_img.png" />
      <div>您还没绑定房间信息哦，<br/>快去绑定吧!~</div>
    </div>
    <div style="flex:1;overflow:auto;">
      <van-list style="padding-top:1px;">
        <div v-for="(item,index) in list" :key="index">
          <div class="cellItem">
            <div class="leftContent">
              <div style="display: flex;align-items: center;padding-right: 10px;" :class="{themeL:bindThemeL}">
                <img
                  src="../../assets/images/personal_center/user_address_icon_location.png"
                />
              </div>
              <div class="textContent">
                <span class="areaName" :class="{areaNameThemeL:bindThemeL}">{{item.AREA_NAME}}</span>
                <span
                  class="houseNum"
                  :class="{houseNumThemeL:bindThemeL}"
                >{{item.BUILD_NAME}}{{item.UNIT_NO == 0 ? "" : "-"+item.UNIT_NO}}-{{item.ROOM_NAME}}</span>
              </div>
            </div>
            <van-button
              class="unBindBtn"
              :class="{unBindBtnThemeL:bindThemeL}"
              :color="item.APPROVAL_STATUS == 0 ? '#FB5768' : '#4A4A4A' "
              round
              plain
              @click="unBindRoom(item)"
            >{{item.APPROVAL_STATUS == 1 ? "解除绑定" : "删除申请"}}</van-button>
          </div>
        </div>
      </van-list>
    </div>
    <div class="addNewBtnSpace">
      <van-button class="addNewBtn" :class="{addNewBtnThemeL:bindThemeL}" color="#FB5768" size="large" round @click="addNewRoom">新增房产</van-button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { getRoomInfo, getRoomInfoNew, deleteRoom, deleteRoomNew } from "../../services/MineService";
import { Toast, Dialog } from "vant";
import { mapState } from "vuex";
import noNetworkImg from '@/assets/images/home/notice/img_net@2x.png'
import NavigationBar from '@/common/currency/NavigationBar.vue';
import {storage} from '../../utils/storage'
export default {
  name: "MyHouses",
  components: {
    NavigationBar
  },
  data() {
    return {
      showEmpty: false,
      list: [],
      networkError: false,
      noNetworkImg: noNetworkImg,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
    };
  },

  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    addNewRoom() {
      this.$router.push("./selectArea");
    },

    initMyRooms() {
      //调用新接口
      if (this.$openVirArea) {
        //暂时在开发联调环境下调用新接口
        this.getRoomInfoNew();
        return;
      }

      let param = { OWNER_CODE: this.$userInfo.OWNER_CODE };
      getRoomInfo(param)
        .then(data => {
          this.networkError = false;
          this.list = data;
          this.showEmpty = true;
        })
        .catch(error => {
          // Toast(error);
          this.networkError = true;
        });
    },
    //获取房间列表（新接口）
    getRoomInfoNew() {
      let param = { ownerCode: this.$userInfo.OWNER_CODE };
      getRoomInfoNew(param)
        .then(result => {
          if (result.resultCode == 0) {
            this.networkError = false;

            //新老接口返回数据字段映射转换
            result.data.forEach((item, index) => {
              item.APPROVAL_RESULT = item.approvalResult;
              item.APPROVAL_STATUS = item.approvalStatus;
              item.AREA_CODE = item.areaCode;
              item.AREA_NAME = item.areaName;
              item.BUILD_NAME = item.buildName;
              item.FLOOR_NO = item.floorNo;
              item.RELATE_FLAG = item.relateFlag;
              item.ROOM_CODE = item.roomCode;
              item.ROOM_NAME = item.roomName;
              item.UNIT_NO = item.unitNo;
            })

            this.list = result.data;
            this.showEmpty = true;
          } else {
            this.$toast(result.msg);
          }
        })
        .catch(error => {
          // Toast(error);
          this.networkError = true;
        });
    },
    unBindRoom(item) {
      Dialog.confirm({
        title:
          item.APPROVAL_STATUS == 1 ? "确定要解除绑定？" : "确定要删除申请？",
        confirmButtonColor: "#FB5768"
      })
        .then(() => {
          this.confirmUnBind(item);
        })
        .catch(() => {
          // on cancel
        });
    },
    confirmUnBind(item) {
      //调用新接口
      if (this.$openVirArea) {
        //暂时在开发联调环境下调用新接口
        this.deleteRoomNew(item);
        return;
      }
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        ROOM_CODE: item.ROOM_CODE
      };
      deleteRoom(param)
        .then(data => {
          this.list.splice(this.list.indexOf(item), 1);
          if (this.isNoHouse) {
            this.resetLocalAreaInfo();
          } else {
            //yuanl add for 解绑房间后回首页需要重新刷新小区
            Vue.prototype.$refreshHomePage = true;
          }
        })
        .catch(error => {
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    //解绑房间（新接口）
    deleteRoomNew(item) {
      let param = {
        ownerCode: this.$userInfo.OWNER_CODE,
        roomCode: item.ROOM_CODE
      };
      deleteRoomNew(param)
        .then(data => {
          this.list.splice(this.list.indexOf(item), 1);
          //yuanl add for 解绑房间后回首页需要重新刷新小区
          Vue.prototype.$refreshHomePage = true;
        })
        .catch(error => {
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    resetLocalAreaInfo() {
      var info = this.$userInfo;
      info.isNoArea = 1; //yuanl modify 不要重置小区信息
      /*info.AREA_CODE = "";
      info.AREA_NAME = "";*/ info.BUILD_CODE = "";
      info.BUILD_NAME = "";
      info.ROOM_CODE = "";
      info.ROOM_NAME = "";
      info.CITY_CODE = "";
      info.CITY_NAME = "";
      console.log(this.$userInfo);
      this.$setUserInfo(info);
      Vue.prototype.$refreshHomePage = true;
      // this.$router.replace("/homePage");
    }
  },
  mounted() {
    this.initMyRooms();
  },
  computed: mapState({
    isNoHouse() {
      var hasHouse = false;
      for (var item of this.list) {
        if (item.APPROVAL_STATUS == 1) {
          hasHouse = true; //qiao 修改ZHSQYYPT-4444
          break;
        }
      }
      return !hasHouse;
    },
  })
};
</script>


<style scoped lang="scss">
/deep/.van-nav-bar .van-icon {
  color: #333333;
}

.emptyView {
  height: 120px;
  text-align: center;
  vertical-align: middle;
}
/*只绑定虚拟小区空列表*/
.noList img {
  margin: 150px 100px 20px 100px;
  width: 180px;
  display: inline-block;
}
.noList div {
  font-family: PingFangSC-Regular;
  letter-spacing: 0;
  text-align: center;
  width: 100%;
  font-size: 16px;
  color: #9A9A9A;
  line-height: 25px;
}

.cellItem {
  height: 88px;
  background-color: #ffffff;
  margin: 15px;
  border-radius: 7px;
  display: flex;
  padding-right: 12px;
  padding-left: 12px;
  justify-content: space-between;
  align-items: center;
}

.leftContent {
  display: flex;
  align-items: center;
}
.leftContent img {
  width: 50px;
  height: 50px;
}
.themeL img{
  width: 60px !important;
  height: 60px !important;
}

.textContent {
  display: flex;
  flex-direction: column;
}

.unBindBtn {
  height: 30px;
  width: 90px;
  line-height: 30px;
  font-size: 12px;
}
.unBindBtnThemeL {
  font-size: 15px !important;
  padding-left: 5px !important;
  padding-right: 5px !important;
}

.areaName {
  font-weight: bold;
  font-size: 15px;
  color: #333333;
  letter-spacing: -0.05px;
}
.areaNameThemeL {
  font-size: 18px !important;
  line-height: 30px;
}

.houseNum {
  font-size: 13px;
  color: #333333;
  letter-spacing: -0.04px;
}
.houseNumThemeL {
  font-size: 16px !important;
  line-height: 30px;
}

.addNewBtnSpace {
  height: 80px;
  bottom: 30px;
}
.addNewBtn {
  width: 78%;
  height: 40px;
  line-height: 40px;
    border: none;
    position: absolute;
    margin-top: 5px;
    margin-left: 11%;
    margin-right: 11%;
}
.addNewBtnThemeL {
  font-size: 18px !important;
}
.no-networking {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 90px;
  font-family: PingFangSC-Regular;
  text-align: center;
img {
  width: 66px;
  margin-bottom: 20px;
}
h3 {
  font-size: 16px;
  color: #888888;
  margin: 10px 0px;
}
p {
  font-size: 14px;
  color: #C4C4C4;
  margin: 0 0 3px;
}
div,
div:active {
  background-color: #ffffff;
  color: rgb(251, 87, 104);
  position: relative;
  margin: 10px;
  border-radius: 20px;
  border: 1px solid rgb(251, 87, 104);
  font-size: 16px;
  height: 40px;
  line-height: 40px;
  width: 125px;
}
div:active{
  opacity: 0.5;
}
}
</style>