<template>
  <div class="phoneDoor">
    <!-- 开门按钮 -->
    <img width="60" class="phoneDoorStyle" :src="isOpening?loadingImg:phoneImg" @click="doorPress()"/>
    <!-- 远程开门弹窗 -->
    <div
      v-show="isPopShow"
      class="phonedoor-popup-container"
      style="background: rgba(0, 0, 0,0.5);"
    >
      <div class="door-popup-body">
        <img :src="closeImg" class="popcloseBtn" @click="closePopup()" />

        <van-image fit="fill" :src="backImg" />
        <div id="qrArea">
          <div class="failText">请对准门禁摄像头</div>
          <div class="QRCode">
            <canvas id="phoneDoorQrcode" class="phoneDoorQrcode"></canvas>
          </div>
          <!-- <div class="QRCodeRefresh hide" style="position: absolute;bottom: 0;width: 100%;">
          <img
            style="position:relative;top: 2px"
            src="img/phonedoor/pic_tips_refresh.png"
            onclick="refreshQRcode()"
          />
          </div>-->

          <div class="lineBoxStyle">
            <div class="lineStyle"></div>
          </div>
        </div>
        <div class="opemDoorBox" v-show="isRemote">
          <div class="failMsg">远程开门</div>
          <div class="share" id="shareScrollbar">
            <div
              class="leftImg"
              v-for="(x,index) in doorList"
              :key="index"
              @click="doorRemoteOpen(x)"
              
            >
              <button class="doorBtn" v-if="x.openType>3">{{x.deviceName}}</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getDoorListService,
  openLinglingRemoteDoorService,
  getLinglingDoorInfoService,
  addRecordService,
  addPointService,
  getLeeLenDoorInfoService,
  openLeeLenRemoteDoorService
} from "../../services/phoneDoorService";
import QRCode from "qrcode"; //引入生成二维码插件
import {store,storage} from '../../utils/storage'
import {mapState} from "vuex";
export default {
  name: "phoneDoor",
  data() {
    return {
      doorList: [], //门禁列表
      isLLQrcode: false, //是否支持令令蓝牙开门
      isLLBluetooth: false, //是否支持令令蓝牙开门
      isLeeLenBluetooth: false, //是否支持立林蓝牙开门
      isRemote: false, //是否支持远程开门
      linglingKeys: "", //令令设备key
      linglingDoorInfos: [], //令令门禁信息
      LLingID: "", //令令设备ID
      qrcode: "", //令令二维码
      qrCanvas: null,
      isPopShow: false, //远程开门弹窗是否显示
      openStart: null, //手机开门开始时间，用于计算开门周期
      isOpening:false,//判断是否在处理手机开门
      popupTimer:null,//弹出框定时器
      btnTimer:null,
      closeImg: require("../../assets/images/home/property/icon_phoneDoor_popup_close.png"),
      phoneImg: require("../../assets/images/home/property/icon_phoneDoor_open.png"),
      backImg: require("../../assets/images/home/property/icon_phoneDoor_popup_bg.png"),
      loadingImg: require("../../assets/images/home/property/icon_phoneDoor_loading.gif"),
      userInfo:{}
    };
  }, 
  computed: {
    //监控刷新手机开门state是否变化
    phoneDoorRefresh(){
      return this.$store.state.home.phoneDoorRefresh;
    }
  },
  watch: {
    //机开门state变化回调 
    phoneDoorRefresh(val) {
      console.log("valvalvalvalvalvalvalvalval"+val);
      if (val) {
        this.$store.dispatch('setPhoneDoorRefreshFlag', false);
        this.doorList=[];
        this.isLLQrcode= false, //是否支持令令蓝牙开门
        this.isLLBluetooth= false, //是否支持令令蓝牙开门
        this.isLeeLenBluetooth= false, //是否支持立林蓝牙开门
        this.isRemote= false, //是否支持远程开门
        this.linglingKeys= "", //令令设备key
        this.linglingDoorInfos= [], //令令门禁信息
        this.LLingID= "", //令令设备ID
        this.qrcode= "", //令令二维码
        this.isPopShow= false, //远程开门弹窗是否显示
        this.openStart= null, //手机开门开始时间，用于计算开门周期
        this.isOpening=false,//判断是否在处理手机开门
        clearTimeout(this.popupTimer);
        clearTimeout(this.btnTimer);
        this.getDoorList();
      }
    }
  },
  mounted() {
    this.qrCanvas = document.getElementById("phoneDoorQrcode");
    this.getDoorList();
  },
  methods: {
    /* *************************************获取开门信息************************************* */
    /* 获取门禁列表 */
    getDoorList() {
      console.log('getDoorList');
      let reqData = {
        NEIGH_NO: this.$userInfo.AREA_NO,
        NEIGH_STRUCTURE: this.$userInfo.ROOM_NO,
        AREA_CODE: this.$userInfo.AREA_CODE,
        BUILD_CODE: this.$userInfo.BUILD_CODE,
        UNIT_CODE: this.$userInfo.UNIT_NO,
        ROOM_CODE:this.$userInfo.ROOM_CODE
      };
      getDoorListService({ data: JSON.stringify(reqData) })
        .then(result => {
          console.log('findOpenType---result');
          console.log(JSON.stringify(result));
          //请求成功
          if (result.success) {
            
            this.doorList = result.doorInfos;
            this.LLingID = result.lingLingId;
            this.findOpenType();
            this.$store.state.home.hasOpenDoor=  true;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 判断小区支持的开门方式 */
    findOpenType() {
      console.log('findOpenType');
      for (let i in this.doorList) {
        let openType = this.doorList[i].openType;
        //奇数，1、3、5、7,支持蓝牙开门
        if ((openType & 1) == 1) {
          console.log("蓝牙开门支持");
          if (this.doorList[i].deviceMan == "令令") {
            this.isLLBluetooth = true;
          } else if (this.doorList[i].deviceMan == "立林") {
            this.isLeeLenBluetooth = true;
          }
        }

        //偶数，支持二维码开门
        if ((2 & openType) == 2) {
          if (this.doorList[i].deviceMan == "令令") {
            console.log("二维码开门支持");
            this.isLLQrcode = true;
          }
        }
        //openType=4，支持远程开门
        if ((4 & openType) == 4) {
          console.log("远程开门支持");
          this.isRemote = true;
        }
      }
      
      if(this.LLingID != "" && this.LLingID !=null){
        this.getLinglingDoorInfo(this.LLingID);
      }
      if(!this.isLLQrcode){
        document.getElementById("qrArea").style.display="none";
      }
    },
    /* 获取令令门禁信息 */
    getLinglingDoorInfo() {
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE,
        BUILD_CODE: this.$userInfo.BUILD_CODE,
        UNIT_CODE: this.$userInfo.UNIT_NO,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getLinglingDoorInfoService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.success) {
            this.linglingDoorInfos = result.doorInfos;
            var doorInfos = result.doorInfos;
            var sdkKeys = "";
            for (var i = 0; i < doorInfos.length; i++) {
              if (i == 0) {
                sdkKeys = sdkKeys + doorInfos[i].sdkKey;
              } else {
                sdkKeys = sdkKeys + "," + doorInfos[i].sdkKey;
              }
            }
            this.linglingKeys = sdkKeys;
            /* ************************************* */
            //调用原生方法，获取令令二维码
            var lingling = cordova.plugins.linglingPlugin;
            console.log("LLingIDwqwq" + this.LLingID);
            if(this.linglingKeys != "" && this.linglingKeys !=null){
              lingling.getQrCode(
                this.LLingID,
                this.linglingKeys,
                resultStatus => {
                  console.log("二维码获取成功" + resultStatus);
                  // 将获取到的数据（val）画到msg（canvas）上
                  console.log("msg获取成功" + this.qrCanvas);
                  console.log("sdkKeys=" + sdkKeys);
                  QRCode.toCanvas(this.qrCanvas, resultStatus);
                },
                function(message) {
                  console.log("二维码获取失败");
                }
              );
            }
            
            /* ************************************* */
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 开门按钮事件 */
    doorPress() {
      console.log('doorPress');
      //设置开门状态
      this.isOpening = true;
      //记录点击开门时间
      this.openStart = new Date();
      //开门按钮动图
      this.btnTimer = setTimeout(() => {
        this.isOpening = false;
      }, 8000);
      //设置两秒钟后显示远程开门弹窗
      setTimeout(() => {
         //如果有二维码或远程开门
        if(this.isLLQrcode){
            this.showRemotePopup();
        } else{
           if(this.doorList.length >1 && this.isRemote)
            this.showRemotePopup();
        }
      }, 2000);
      //无门禁权限
      if (this.doorList == null || this.doorList.length == 0){
        this.$toast("该小区无门禁权限!");
        this.closePopup();
        return;
      }
      //既有令令也有立林开门
      if (this.isLLBluetooth && this.isLeeLenBluetooth) {
          this.openLLBlueTooth();
      }
      //有令令或立林一种开门方式
      else if(this.isLLBluetooth || this.isLeeLenBluetooth){
        if (this.isLLBluetooth){
          this.openLLBlueTooth();
        }
        if (this.isLeeLenBluetooth) {
          this.openLeeLenBlueTooth();
        }
      }else{
        //不支持蓝牙，则调用远程开门，判断是否只有一个门
        this.isOnlyOneDoorRemote();
      }
    },
    /* *************************************蓝牙开门************************************* */
    /* 令令蓝牙开门 */
    openLLBlueTooth() {
      var lingling = cordova.plugins.linglingPlugin;
      lingling.btOpenDoorMethod(
        this.linglingKeys,
        resultStatus => {
          console.log("======linglinglanyachenggong");
          this.closePopup();

          this.$toast({
            message: "开门成功",
            onClose: () => {
              //  获取数据令令门数据 并记录用户开门数据
              for (var i = 0; i < this.linglingDoorInfos.length; i++) {
                console.log("进入for循环 resultStatus=" + resultStatus);
                // 循环找到设备
                if (this.linglingDoorInfos[i].deviceCode == resultStatus) {
                  console.log(
                    "匹配到 deviceCode=" + this.linglingDoorInfos[i].deviceCode
                  );
                  for (var j = 0; j < this.doorList.length; j++) {
                    console.log("进入for二层循环");
                    //循环获取设备id等信息
                    if (
                      this.linglingDoorInfos[i].sdkKey ==
                      this.doorList[j].sdkKey
                    ) {
                      console.log("匹配到 sdkKey=" + this.doorList[j].sdkKey);
                      //  记录开门动作
                      this.addRecord(
                        this.doorList[j].deviceId,
                        this.doorList[j].deviceName,
                        1,
                        2,
                        0,
                        "",
                        1,
                        "开门成功"
                      );

                      this.addPoint();
                    }
                  }
                }
              }
            }
          });
        },
        message => {
          this.closePopup();
          console.log("开门失败" + message);
          // 记录失败开门动作
          this.addRecord("无设备信息", "无设备信息", 1, 2, 0, "", 0, message);
          if(this.isLeeLenBluetooth){
            this.openLeeLenBlueTooth();
            return;
          }
          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("开门失败，请重试");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
      );
    },
    /* 立林蓝牙开门 */
    openLeeLenBlueTooth() {
      var reqData = {
        neigh_no: this.$userInfo.AREA_NO,
        neigh_structure: this.$userInfo.ROOM_NO,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getLeeLenDoorInfoService({ data: JSON.stringify(reqData) })
        .then(res => {
          if (res.success == "success") {
            var llData = res.door_entry;
            llData = JSON.stringify(llData);
            var obj = JSON.parse(llData);
            obj.isLLBluetooth=this.isLLBluetooth;
            if (/android/.test(navigator.userAgent.toLowerCase())) {
              this.leelenAndroid(obj);
            } else {
              this.leelenIos(obj);
            }
          }
        })
        .catch(err => {
          console.log(JSON.stringify(err));
          this.$toast("网络异常，请稍后重试！");
        });
    },
    leelenIos(obj) {
      /* 发送蓝牙开门通知 */
      window.broadcaster.fireNativeEvent(
        "openLeelenDoor",
        { openCode: obj },
        function() {
          console.log("立林蓝牙开门发起成功");
        }
      );

      var leelenBlueFlag = 0;
      // 蓝牙开门结果返回监听;
      window.broadcaster.addEventListener("LeeLenResultCode", data2 => {
        if (data2.code == "0" && leelenBlueFlag == 0) {
          this.$toast({
            message: "开门成功",
            onClose: () => {
              //  记录开门动作
              this.addRecord(
                data2.DEVICE_ID,
                data2.DEVICE_NAME,
                1,
                1,
                0,
                "",
                1,
                "开门成功"
              );

              this.addPoint();
            }
          });

          this.closePopup();
        } else if (leelenBlueFlag == 0) {
          console.log("开门失败");
          this.closePopup();
          //  记录失败开门动作
          var errMsg = this.getLeelenErrorMsg(data2.code);
          this.addRecord("无设备信息", "无设备信息", 1, 1, 0, "", 0, errMsg);

          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("开门失败，请重试");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
        leelenBlueFlag = 1;
      });
    },
    leelenAndroid(obj) {
      var leelen = cordova.plugins.LeeLenPlugin;
      leelen.access(
        obj,
        resultStatus => {
          console.log("开门成功，line 335!!!!!_+_+_+_+_ " + resultStatus);
          this.$toast({
            message: "开门成功",
            onClose: () => {
              this.addRecord(
                resultStatus.deviceId,
                resultStatus.deviceName,
                1,
                1,
                0,
                "",
                1,
                "开门成功"
              );

              this.addPoint();
            }
          });
          
          this.closePopup();
        },
        message => {
          console.log("开门失败===countFlag2=== " + message);
          //  记录失败开门动作
          var errMsg = this.getLeelenErrorMsg(message);
          this.addRecord("无设备信息", "无设备信息", 1, 1, 0, "", 0, errMsg);
          this.closePopup();
          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("开门失败，请重试");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
      );
    },
    getLeelenErrorMsg(message){
      var errMsg = "";
      switch (message) {
        case "10001":
          errMsg = "10001:蓝牙关闭";
          break;
        case "10002":
          errMsg = "10002:连接超时";
          break;
        case "10003":
          errMsg = "10003:未搜索到设备";
          break;
        case "10004":
          errMsg = "10004:蓝牙连接失败";
          break;
        case "10005":
          errMsg = "10005:蓝牙码错误";
          break;
        case "10006":
          errMsg = "10006:设备没有开门权限";
          break;
        case "10007":
          errMsg = "10007:设备版本不支持";
          break;
      }
      return errMsg;
    },
    /* *************************************远程开门************************************* */
    showRemotePopup() {
      this.isPopShow = true;
      this.popupTimer = setTimeout(() => {
        this.addPoint();
        this.closePopup();
      }, 15000);
    },
    closePopup() {
      this.isOpening = false;
      this.isPopShow = false;
      clearTimeout(this.popupTimer);
      clearTimeout(this.btnTimer);
    },
    /* 判断如果只有一个远程门禁，默认直接开启 */
    isOnlyOneDoorRemote(){
      if(this.doorList.length == 1 && this.isRemote)
        this.doorRemoteOpen(this.doorList[0]);
    },
    /* 远程开门点击 */
    doorRemoteOpen(obj) {
      if (obj.deviceMan == "令令") {
        this.openLinglingRemoteDoor(obj);
      } else {
        this.openLeeLenRemoteDoor(obj);
      }
    },
    /* 令令远程开门 */
    openLinglingRemoteDoor(door) {
      let reqData = {
        sdkKey: door.sdkKey.toString()
      };
      openLinglingRemoteDoorService({ data: JSON.stringify(reqData) })
        .then(result => {
          console.log(JSON.stringify(result));
          if (result.success) {
            //令令远程开门成功
            this.closePopup();
            this.$toast({
              message: "开门成功",
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  2,
                  0,
                  "",
                  1,
                  "开门成功"
                );
                this.addPoint();
              }
            });
          } else {
            //令令远程开门失败
            this.closePopup();
            this.$toast({
              message: result.msg,
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  2,
                  0,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 立林远程开门 */
    openLeeLenRemoteDoor(door) {
      let reqData = {
        neigh_no: door.neigh_no.toString(),
        neigh_structure: door.neigh_structure.toString(),
        device_no: door.deviceId.toString(),
        read_head_no: door.read_head_no.toString()
      };
      openLeeLenRemoteDoorService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.success == "success") {
            //立林远程开门成功
            this.closePopup();
            console.log("远程开门成功");
            this.$toast({
              message: "开门成功",
              onClose: () => {
                console.log("toast close");
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  1,
                  0,
                  "",
                  1,
                  "开门成功"
                );

                this.addPoint();
              }
            });
          } else {
            //立林远程开门失败
            this.closePopup();
            this.$toast({
              message: "远程开门失败，请重试",
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  1,
                  0,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },

    /* *************************************开门后处理************************************* */
    /* 手机开门记录 */
    addRecord(
      deviceId,
      deviceName,
      openType,
      deviceMan,
      doorType,
      duration,
      openSucceed,
      remarks
    ) {
      //返回开门结果
      var phoneModel = device.model;
      var phoneType = device.platform;

      //记录点击开门时间
      var openEnd = new Date();
      duration = openEnd.getTime() - this.openStart.getTime();
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE, //小区代码
        areaName: this.$userInfo.AREA_NAME, //小区名称
        buildCode: this.$userInfo.BUILD_CODE, //楼座代码
        buildName: this.$userInfo.BUILD_NAME, //楼座名称
        unitNo: this.$userInfo.UNIT_NO, //单元号
        roomCode: this.$userInfo.ROOM_CODE, //房间代码
        ownerCode: this.$userInfo.OWNER_CODE, //业主代码
        ownerName: this.$userInfo.OWNER_NAME, //业主名称
        ownerNickName: this.$userInfo.OWNER_NICK_NAME, //业主昵称
        phone: this.$userInfo.PHONE, //手机号
        phoneModel: phoneModel, // 开门手型号
        phoneType: phoneType, // 开门手机类型
        deviceId: deviceId, // 设备编号
        deviceName: deviceName, // 设备名称
        openType: openType, // 开门方式：1蓝牙，2远程开门，3二维码，0其它
        deviceMan: deviceMan, //设备厂家 0其它 1立林 2令令 3业主宝 4海康
        doorType: doorType, //门口类型 0大门 1单元门
        duration: duration, //开门话费时间
        openSucceed: openSucceed, //开门是否成功 0失败 1成功
        remarks: remarks //结果备注
      };
      if (this.$userInfo.AREA_NO == "" || this.$userInfo.AREA_NO == undefined) {//确认门禁类型 是否有AREA_NO
          reqData.DOOR_TYPE = "1";
        } else {
          reqData.DOOR_TYPE = "0";//门禁类型：0立林开门,1普通开门;默认普通开门
        }
      reqData.DOOR_TYPE = "0";
      addRecordService(reqData)
        .then(result => {
          //请求成功
          if (result.success) {
            //添加记录成功
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
        });
    },
    addPoint() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        behaviourNo: "1002",
        ownerCode: this.$userInfo.OWNER_CODE,
        appVersion: "3.1.0.0"
      };
      addPointService(reqData)
        .then(result => {
          //请求成功
          if (result.code == 200) {
            this.$toast("恭喜手机开门获取" + result.data + "积分");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    }
    /* *************************************开门后处理************************************* */    
  }
};
</script>

